:root {
    font-family: 'candelabook', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'arvoregular', serif;
}

h1 {
    font-size: 3.5rem;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-size: 1.2rem;
}


section {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 5vw;
    padding-right: 5vw;
    margin-bottom: 15vh;
}

.icon {
    width: auto;
}

.bio {
    width: 100vw;
}

.bio > img {
    box-shadow: 3px 3px 5px 1px black;
}

.name {
    color: white;
    text-shadow: 1px 5px 4px rgba(0, 0, 0, 0.473);
}

.about > h2 {
    text-align: center;
}

.about > p {
    font-size: 1.4rem;
}

.project {
    box-shadow: 1px 5px 10px 2px #c9c7c7;
}

.project-screenshot > img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.project-links > .icon {
    height: 1.2rem;
    width: auto;
}

.project-links, .project-footer {
    display: flex;
}

.project-footer {
    justify-content: space-between;
    align-items: flex-start;
}

.body-text {
    padding: 10px 20px 45px;
}

.work {
    gap: 5vh;
}

.contact {
    background-color: #0e3007bd;
    color: white;
}


/* mobile phone */

@media (max-width: 768px) {

    .icon {
        height: 2.5rem;
    }

    .bio {
        align-items: center;
        background: linear-gradient(150deg, #0e3007, #0e3007 32%, rgb(207, 205, 205) 32%, white 33.25%, white);
        padding: 3vh;
    }

    .name {
        position: relative;
        top: -7rem;
        left: 2rem;
        line-height: 3.5rem;
    }

    .about {
        width: 100%;
        position: relative;
        top: -10vh;
    }

    .work {
        padding-bottom: 10vh;
    }

    .project-footer {
        padding: 5px 20px;
    }

    .contact {
        padding: 20px 25px 0 25px;
        margin-bottom: 0;
    }

    .contact-details {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .contact-details > h2 {
        text-align: center;
    }

    .invite {
        font-size: 1.5rem;
        margin: 20px 0;
    }

    .contact-info {
        width: 100%;
    }

    .social-icons {
        padding: 3vh 0 12vh 0;
        display: flex;
        width: 50%;
        justify-content: space-around;
    }

    .personal-photo {
        width: 100vw;
        padding: 0;
        margin: 0;
    }

    .svg-icon {
        display: none;
    }
}

/* tablet */

@media only screen and (min-width: 768px) and (max-width: 1279px) {

    .bio {
        display: block;
        background: linear-gradient(160deg, #0e3007, #0e3007 55%, rgba(207, 205, 205, 0.836) 55%, white 55.5%, white);
        position: relative;
        height: 100vh;
        z-index: -1;
        align-items: start;
        justify-content: start;
        padding-top: 7vh;
    }

    .bio > * {
        display: block;
        position: relative;
    }

    .bio > img {
        width: 45vw;
        height: 50vw;
        object-fit: cover;
        float: left;
        margin-right: 25px;
    }

    .about {
        background-color: white;
        height: 90vh;
        width: 75vw;
        z-index: -1;
        box-shadow: 3px 3px 5px 1px #0e300738;
        margin: auto;
        padding: 80px 35px;
    }

    .name {
        position: relative;
        margin: 10px 0;
        left: -7vw;
        z-index: 1;
    }

    .bio > .social-icons {
        display: none;
    }

    .work {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: stretch;
        justify-content: space-between;
        width: 85vw;
        margin: auto;
        padding-bottom: 25vh;
    }

    .work > .section-header {
        width: 100%;
    }

    .project {
        width: 47%;
    }

    .project-screenshot {
        height: 30vh;
    }

    .project-footer {
        padding: 10px 20px;
    }

    .contact {
        display: flex;
        flex-direction: row;
        margin-bottom: 0;
        padding: 5vh 5vh 5vh 8vh;
        justify-content: space-between;
        height: 90vh;
    }

    .contact-details p {
        font-size: 1.5rem;
    }

    .invite {
        margin: 15px 0;
    }

    .address {
        margin: 30px 0;
    }

    .contact-details > .social-icons {
        display: flex;
        justify-content: space-between;
        width: 30%;
        margin-top: 2rem;
    }

    .email, .phone {
        display: flex;
        align-items: center;
        gap: 2vw;
    }
    
    .icon {
        height: 3.5rem;
    }

    .svg-icon {
        height: 1.5rem;
        fill: white;
        float: left;
    }

    .personal-photo > img {
        height: 74vh;
    }
}

