        font-size: 1.5rem;
    }

    .invite {
        margin: 15px 0;
    }

    .address {
        margin: 30px 0;
    }

    .contact-details > .social-icons {
        display: flex;
        justify-content: space-between;
        width: 30%;
        margin-top: 2rem;
    }

    .email, .phone {
        display: flex;
